
const divSocial = document.querySelector(".home__social");

const socialApps = ( url, iconHtml ) => {

    const socialHtml = `
    <a href="${url}" target="_blank" class="home__social-icon">
        ${iconHtml}
    </a>
    `;

    const asocialHtml = document.createElement('div');
    asocialHtml.innerHTML = socialHtml;

    divSocial.append(asocialHtml);

}


export {

    socialApps,

}


