

const setTagClassIconHtml = ( iconHtml, setClass ) => {

    /* ADD CLASS skills__icon TO ICONS */
    const getIconHTML = document.createElement('div');
    getIconHTML.innerHTML = iconHtml;

    if (getIconHTML.children[0]) {
        
        const iconClass = document.getElementsByClassName(`${getIconHTML.children[0].classList[1]}`);
        for (const iterator of iconClass) {
            iterator.classList.add( setClass );
        }


    }

}


export {

    setTagClassIconHtml,

}

