/* 1.25.20 */

import { setTagClassIconHtml } from "./functions-component";

const   servicesContainer  = document.querySelector('.services__container');

let     serviceContent;

const createServicesHTML = ( id, iconHtml, textP1, textP2, objServices ) => {

    const servicesHTML = `
    <!-- ===== Service ${id} ===== -->
    <div class="services__content">

        <div>
            ${iconHtml}
            <h3 class="services__title">${textP1} <br> ${textP2}</h3>
        </div>

        <span class="button button--flex button--small button--link services__button">
            View more
            <i class="uil uil-arrow-right button__icon"></i>
        </span>

        <div class="services__modal">
            <div class="services__modal-content">
                <h4 class="services__modal-title">${textP1} <br> ${textP2}</h4>
                <i class="uil uil-times services__modal-close"></i>

                <ul class="services__modal-services modal__services-${id} grid">



                </ul>

            </div>
        </div>

    </div>
    `;


    const divHtmlServices = document.createElement('div');
    divHtmlServices.innerHTML = servicesHTML;

    servicesContainer.append(divHtmlServices);

    setTagClassIconHtml(iconHtml, 'services__icon')
    

    serviceContent = document.querySelector(`.modal__services-${id}`);

    for (const [id, data] of Object.entries(objServices)) {
        modalContentHTML(data);
    }
    
    modalViewState();
}

const modalContentHTML = ( text ) => {

    const modalContent = `
    <li class="services__modal-service">
        <i class="uil uil-check-circle services__modal-icon"></i>
        <p>${text}</p>
    </li>
    `;

    const divModalContent = document.createElement('div');
    divModalContent.innerHTML = modalContent;

    serviceContent.append(divModalContent);

};

const modalViewState = () => {

    const   modalViews  = document.querySelectorAll('.services__modal'),
            modalBtns   = document.querySelectorAll('.services__button'),
            modalCloses = document.querySelectorAll('.services__modal-close');

    let modal = ( modalClick ) => {
        modalViews[modalClick].classList.add('active-modal');
    }


    modalBtns.forEach( (modalBtn, i) => {
        modalBtn.addEventListener('click', () => {
            modal(i);
        });
    });

    modalCloses.forEach( (modalClose) => {
        modalClose.addEventListener('click', () => {
            modalViews.forEach( (modalView) => {
                modalView.classList.remove('active-modal');
            });
        });
    });

}



export {
    createServicesHTML,
}