import { jsSwiperNormal } from "./swipers-component";


const portfolioContainer = document.querySelector('.swiper-wrapper');

const creatPorfolioContainerHTML = ( id, urlImg, text ) => {

  const PorfolioContainerHTML = `
                    
    <!-- ===== Portfolio ${id} ===== -->
 
        <img src="${urlImg}" alt="" class="portfolio__img">
        <div class="portfolio__data">
            <h3 class="portfolio__title">Brand</h3>
            <p class="portfolio__description">${text}</p>
            
            <a href="#" class="button button--flex button--small portfolio__button">
                Demo
                <i class="uil uil-arrow-right button__icon"></i>
            </a>

        </div>


  `;

  const divPorfolioContainerHTML = document.createElement('div');
  divPorfolioContainerHTML.innerHTML = PorfolioContainerHTML;
  divPorfolioContainerHTML.classList.add('portfolio__content');
  divPorfolioContainerHTML.classList.add('grid');
  divPorfolioContainerHTML.classList.add('swiper-slide');
   

  portfolioContainer.append(divPorfolioContainerHTML);



  jsSwiperNormal();

}




export {
  creatPorfolioContainerHTML,

}


